  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.link-effect::before, .link-effect::after {
  position: absolute;
  content: "";
}

body {
  font-family: "Handjet", "comic" sans-serif;
  color: #FFFFFF;
  font-weight: 400;
}

a {
  transition: 200ms all cubic-bezier(0.49, 0.025, 0.49, 1);
  text-decoration: none;
  color: #0094FF;
}
a:hover {
  color: #0094FF;
}

b,
strong {
  font-weight: 700;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}
.container {
  max-width: 1431px;
  padding-left: 32px;
  padding-right: 32px;
  margin: 0 auto;
}
@media (max-width: 991px) {
  .container {
    padding-left: 16px;
    padding-right: 16px;
  }
}

.collapse {
  overflow: hidden;
  transition: height 300ms cubic-bezier(0.49, 0.025, 0.49, 1);
}
.collapse:not(.active) {
  height: 0;
}

[data-collapse] .collapse-arrow,
[data-toggler] .collapse-arrow {
  transition: 300ms all cubic-bezier(0.49, 0.025, 0.49, 1);
}
[data-collapse].active .collapse-arrow,
[data-toggler].active .collapse-arrow {
  transform: rotate(-180deg);
}

.link-effect {
  position: relative;
  white-space: nowrap;
}
.link-effect::before, .link-effect::after {
  bottom: 2px;
  width: 100%;
  transform: scaleX(0);
  height: 1px;
  will-change: transform;
  left: 0;
  background-color: currentColor;
  transition: transform 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.link-effect::before {
  opacity: 0;
  transform-origin: left;
}
.link-effect::after {
  opacity: 1;
  transform-origin: right;
}
.link-effect:hover::before, .link-effect:hover::after {
  transform: scaleX(1);
}
.link-effect:hover::before {
  opacity: 1;
}
.link-effect:hover::after {
  opacity: 0;
}

.animate-up {
  transform: translateY(15px);
  opacity: 0;
  transition: opacity 0.65s cubic-bezier(0.42, 0, 0.002, 1), transform 0.65s cubic-bezier(0.42, 0, 0.002, 1);
}

